"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import { Skeleton } from "@/components/ui/skeleton";
import { AffiliateDashboardProvider, useAffiliateDashboardContext } from "./_context/affiliate-dashboard-context";
import type { AffiliateStats } from "./_hooks/use-affiliate-dashboard";

const navLinks = [
  { href: "/dashboard/affiliate", label: "Overview", description: "Performance dashboard and quick actions" },
  { href: "/dashboard/affiliate/payouts", label: "Payouts", description: "Submit and track payout requests" },
  { href: "/dashboard/affiliate/commissions", label: "Commissions", description: "Review commission history" },
  { href: "/dashboard/affiliate/affiliates", label: "Affiliates", description: "Manage your referred pharmacies" },
  { href: "/dashboard/affiliate/register", label: "Register pharmacy", description: "Create a new referral profile" },
];

export default function AffiliateLayout({ children }: { children: React.ReactNode }) {
  return (
    <AffiliateDashboardProvider>
      <AffiliateShell>{children}</AffiliateShell>
    </AffiliateDashboardProvider>
  );
}

function AffiliateShell({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();
  const { loading, error, stats, monthLabel, payoutPercent } = useAffiliateDashboardContext();

  if (loading) {
    return (
      <div className="mx-auto flex max-w-6xl flex-col gap-6 px-4 py-10">
        <div className="grid gap-4 lg:grid-cols-[minmax(0,280px),1fr]">
          <Skeleton className="h-72 rounded-3xl" />
          <div className="space-y-4">
            <Skeleton className="h-14 rounded-3xl" />
            <Skeleton className="h-64 rounded-3xl" />
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return <div className="mx-auto max-w-3xl px-6 py-16 text-sm text-red-600">{error}</div>;
  }

  const activeLink = navLinks.find((link) => pathname === link.href || pathname.startsWith(`${link.href}/`));

  return (
    <div className="min-h-screen bg-gradient-to-b from-white via-emerald-50/30 to-white">
      <div className="mx-auto flex max-w-6xl flex-col gap-6 px-4 py-8 lg:flex-row lg:items-start">
        <AffiliateSidebar stats={stats} monthLabel={monthLabel} payoutPercent={payoutPercent} />
        <section className="flex-1 rounded-3xl bg-white/95 shadow-lg shadow-emerald-100/30 ring-1 ring-emerald-100/60 backdrop-blur">
          <header className="space-y-4 border-b border-emerald-100/80 px-6 py-6">
            <div className="flex flex-wrap items-start justify-between gap-4">
              <div>
                <p className="text-xs font-semibold uppercase tracking-[0.28em] text-emerald-500">Affiliate dashboard</p>
                <h1 className="mt-2 text-2xl font-semibold text-emerald-900 md:text-3xl">{activeLink?.label ?? "Overview"}</h1>
                <p className="mt-2 text-sm text-emerald-700/80">{activeLink?.description ?? "Review performance and manage your referral activities."}</p>
              </div>
              <div className="flex flex-wrap items-center gap-2 text-xs font-medium text-emerald-600">
                <span className="rounded-full bg-emerald-50 px-3 py-1">Period: {monthLabel}</span>
                <span className="rounded-full bg-emerald-50 px-3 py-1">Commission rate: {payoutPercent}%</span>
              </div>
            </div>
          </header>
          <div className="px-6 py-6">
            {children}
          </div>
        </section>
      </div>
    </div>
  );
}

function AffiliateSidebar({
  stats,
  monthLabel,
  payoutPercent,
}: {
  stats: AffiliateStats;
  monthLabel: string;
  payoutPercent: number;
}) {
  const pathname = usePathname();

  return (
    <aside className="w-full max-w-full shrink-0 rounded-3xl bg-white/95 p-6 shadow-lg shadow-emerald-100/20 ring-1 ring-emerald-100/60 backdrop-blur lg:sticky lg:top-8 lg:max-w-xs">
      <div className="space-y-6">
        <div className="space-y-3 text-emerald-900">
          <span className="inline-flex items-center gap-2 rounded-full bg-emerald-50 px-3 py-1 text-xs font-semibold uppercase tracking-wide text-emerald-600">
            Partner dashboard
          </span>
          <h2 className="text-2xl font-semibold tracking-tight">Momentum snapshot</h2>
          <p className="text-sm text-emerald-700/80">
            {monthLabel} · Commission rate {payoutPercent}% · Keep sharing your referral links to stay on pace.
          </p>
        </div>

        <div className="grid gap-3 rounded-2xl bg-gradient-to-br from-emerald-500 via-emerald-600 to-teal-600 p-4 text-white">
          <MetricStat label="Referrals" value={stats.referrals.toLocaleString()} />
          <MetricStat label="Pending payout" value={formatCurrency(stats.pendingPayout)} />
          <MetricStat label="Paid to date" value={formatCurrency(stats.paidPayout)} />
          <MetricStat label="This month" value={formatCurrency(stats.currentCommission)} />
        </div>

        <nav className="space-y-2 text-sm">
          {navLinks.map((link) => {
            const active = pathname === link.href || pathname.startsWith(`${link.href}/`);
            return (
              <Link
                key={link.href}
                href={link.href}
                className={cn(
                  "group block rounded-2xl border px-4 py-3 transition-all",
                  active
                    ? "border-transparent bg-gradient-to-r from-emerald-500/20 to-teal-400/20 text-emerald-900 shadow-sm"
                    : "border-emerald-100/60 bg-white/70 text-emerald-800 hover:border-emerald-200 hover:bg-emerald-50/70"
                )}
              >
                <div className="font-semibold tracking-tight">{link.label}</div>
                <p className="text-xs text-emerald-700/70 transition group-hover:text-emerald-700">{link.description}</p>
              </Link>
            );
          })}
        </nav>

        <div className="rounded-2xl border border-dashed border-emerald-200/70 bg-emerald-50/60 p-4 text-xs text-emerald-700">
          Tip: keep your links active and rotate them regularly to ensure eligibility for monthly payouts.
        </div>
      </div>
    </aside>
  );
}

function MetricStat({ label, value }: { label: string; value: React.ReactNode }) {
  return (
    <div className="rounded-2xl bg-white/10 p-3 shadow-sm shadow-black/5">
      <p className="text-[11px] font-semibold uppercase tracking-[0.3em] text-white/70">{label}</p>
      <p className="mt-2 text-lg font-semibold text-white">{value}</p>
    </div>
  );
}

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}
